import Header from './Header'
import Galleria from './Galleria'

const layoutStyle = {
  margin: 20,
  padding: 20,
  border: '1px solid #DDD'
}

const galleriaTheme = () => (
  Galleria.loadTheme('https://cdnjs.cloudflare.com/ajax/libs/galleria/1.5.7/themes/classic/galleria.classic.min.js').run('.galleria')
)

const Layout = (props) => (
  <div style={layoutStyle}>
    <Galleria onLoad={this.galleriaTheme}/>
    <Header />
      {props.children}
  </div>
)

export default Layout



